.orderItem {
  display: flex;
  padding: 11px 10px 11px 15px;
  justify-content: space-between;
  align-items: center;
}

.orderItem__picContainer {
  padding-right: 10px;
  display: inline-block;
}

.orderItem__pic {
  width: 80px;
  height: 80px;
  border: rgb(206, 44, 57) solid 1px;
}

.orderItem__content {
  display: inline-block;
  width: calc(100% - 100px);
}

.orderItem__product {
  max-width: 237px;
  font-size: 15px;
  font-weight: 600;
  color: #111;
  padding-top: 6px;
  padding-right: 18px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.orderItem__shop {
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 12px;
  color: #777;
  padding-top: 9px;
  padding-bottom: 13px;
  margin-left: 10px;
}

.orderItem__detail {
  display: flex;
  justify-content: space-between;
  height: 22px;
  line-height: 22px;
  padding-right: 4px;
}

.orderItem__price {
  font-size: 13px;
  color: #777;
  vertical-align: 1px;
  margin-left: 10px;
}

.orderItem__price::before {
  content: "\A5";
  margin-right: 1px;
}

.orderItem__btn {
  width: 80px;
  height: 22px;
  text-align: center;
  color: #fff;
  border: 0;
  font-size: 14px;
}

.orderItem__btn--red {
  background-color: #e9203d;
}

.orderItem__btn--grey {
  background-color: #999;
}

.orderItem__commentContainer {
  width: 97%;
  margin: 0 auto;
}
.orderItem__comment {
  width: 100%;
  height: 100px;
}
.orderItem__star--light {
  color: #fa1d00;
}
